@font-face {
  font-family: "MarkOT-Medium";
  src: url("../public/fonts/MarkOT-Medium.otf") format(opentype);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "MarkOT-Bold";
  src: url("../public/fonts/MarkOT-Bold.otf") format(opentype);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "MarkOT-Light";
  src: url("../public/fonts/MarkOT-Light.otf") format(opentype);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "MarkOT";
  src: url("../public/fonts/MarkOT.otf") format(opentype);
  font-weight: 400;
  font-style: normal;
}


body {
  overflow-x: hidden;
  font-family: "MarkOT-Medium";
}

.dflex {
  display: flex;
}

::-webkit-scrollbar {
  width: 3px;
}

::-webkit-scrollbar-thumb {
  background-color: #2c2b83;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #2c2b83;
}

::-webkit-scrollbar-track {
  background-color: #f0f0f0;
}